	module.exports = {"data":{"markdownRemark":{"headings":[],"html":"<p>The book I have chosen for this project is one I’ve read many times called Need by Carrie Jones. It was one of the first ones I thought of, that wouldn’t be too hard to film because I wouldn’t need to have a human involved. I am going to be filming significant objects and environments that the book references to make the viewer interested in what is so special about this objects.</p>\n<p>youtube <a href=\"https://www.youtube.com/watch?time_continue=78&#x26;v=4Ka8XiVvRmI\">https://www.youtube.com/watch?time_continue=78&#x26;v=4Ka8XiVvRmI</a></p>\n<p>The book is about a girl named Zara White who suspects a creepy guy is semi-stalking her. She has an obsession with phobias and is constantly naming them. She has been acting weird since her stepfather died and is sent to Maine to live with her grandmother to get her to open up. Sadly, the semi-stalker is not a figment of Zara’s overactive imagination. He follows her and leaves an eerie trail of gold dust everywhere he places his foot. There is something not human in the small town of Maine and all heads are turned toward Zara. The book delivers romance, suspense, and a creature you’d never thought you’d fear.</p>","frontmatter":{"title":"Book Trailer - Need","graduate":"Stephanie Walser","category":"Time Based Communication","category2":null,"category3":null,"image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/static/screen shot 2018-03-30 at 3.21.51 pm-21843b85462d44c4899fb4734dd28499-a7a62.jpg","srcSet":"/static/screen shot 2018-03-30 at 3.21.51 pm-21843b85462d44c4899fb4734dd28499-5a1a9.jpg 375w,\n/static/screen shot 2018-03-30 at 3.21.51 pm-21843b85462d44c4899fb4734dd28499-0908c.jpg 750w,\n/static/screen shot 2018-03-30 at 3.21.51 pm-21843b85462d44c4899fb4734dd28499-a7a62.jpg 1500w,\n/static/screen shot 2018-03-30 at 3.21.51 pm-21843b85462d44c4899fb4734dd28499-83d7d.jpg 2000w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}}},"pathContext":{"slug":"/work/book-trailer-need/","name":"/Book Trailer - Need/"}}

